.set {
    width: 100%;
}

.set .budget {
    width: 100%;
    height: 130px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.set .budget .left {
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.set .budget .right {
    width: 45%;
    height: 80%;
    text-align: center;
}

.set .title {
    display: flex;
    justify-content: space-around;
}

.set .title .titleLeft {
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.set .title .titleRight {
    width: 45%;
    display: flex;
    justify-content: center;
    align-items: center;
}